import React, {useState, useEffect} from 'react'
import { PanelHeader, Panel, Card, Input, Checkbox, Button, Group, Header, Spinner,
  Snackbar, Avatar, Footer} from '@vkontakte/vkui'
import {Icon16ErrorCircleOutline, Icon16Done} from '@vkontakte/icons'

import UserUrl from './UserUrl'
import bridge from '@vkontakte/vk-bridge'

import './Home.css'

//<Footer className='footer'>Может быть показана реклама</Footer>

const Home = ({id, token, setModal, setPopout, go, setToken}) => {
    const [userLinks, setUserLinks] = useState(<Spinner size="medium" style={{ margin: '20px 0' }} />)
    const [snackbar, setSnackbar] = useState(null)
    const [upload, setUpload] = useState(false)
    const [blocked, setBlocked] = useState(0)
    const [offset, setOffset] = useState(0)

    const restart = () => {
      setUpload(!upload)
    }

    const newOffset = (x) => {
      if (x === 1) {
        setOffset(offset + 10)
      } else {
        if (offset < 10) return;
        setOffset(offset - 10)
      }
      restart()
      return true
    }

    const showBanner = () => {
      let x = Math.round(Math.random() * 3)
      if (x = 2) {
        bridge.sendPromise('VKWebAppShowNativeAds', { ad_format: 'interstitial' })
      }
    }

    const newLink = async () => {
      try {
      let item = document.getElementById('home-input')
       const photos = await bridge.sendPromise('VKWebAppCallAPIMethod', {
            method: 'utils.getShortLink',
            request_id: '324nnefj',
    
            params: {
              v: '5.95',
              url: item.value,
              private: blocked,
              access_token: token.access_token,
            },
        })
        
        setSnackbar(
          <Snackbar
              onClose={() => setSnackbar(null)}
              before={<Avatar size={24} style={{ background: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
            >
              Ссылка сохранена!
            </Snackbar>
            )
      restart()
      document.getElementById('home-input').value = ''
      //showBanner()
      } catch (e) {
        let text = 'Ошибка! Попробуйте позже!'
        if (e.error_data.error_code === 100) {
          text = 'Ошибка! Недопустимый URL'
        }
        if (e.error_data.error_reason) {
          if (e.error_data.error_reason.error_code === 100) {
            text = 'Ошибка! Недопустимый URL'
          }
        }
        setSnackbar(<Snackbar
          onClose={() => setSnackbar(null)}
          before={<Avatar size={24} style={{ background: 'var(--destructive)' }}>
              <Icon16ErrorCircleOutline fill="#fff" width={14} height={14} />
              </Avatar>}
        >
            {text}
        </Snackbar>)
      }
  }

    useEffect(() => {

      const getToken = async () => {
        const access_token = await bridge.send('VKWebAppGetAuthToken', {
          app_id: 7790406,
          scope: '',
      })
        setToken(access_token)
      }
      getToken()
    }, [])

    useEffect(() => {
      if (token.access_token === 0) return
      document.querySelector('#home-input').addEventListener('keydown', function(e) {
        if (e.keyCode === 13) {
          newLink()
        }
      });
        const start = async () => {
    
            try {
              const photos = await bridge.sendPromise('VKWebAppCallAPIMethod', {
                method: 'utils.getLastShortenedLinks',
                request_id: '324nnefj',
        
                params: {
                  v: '5.95',
                  count: 11,
                  offset,
                  access_token: token.access_token,
                },
                })
                
                let items = JSON.parse(JSON.stringify(photos.response.items.length))
                if (items === 11) {
                  photos.response.items.pop()
                }
                

                setUserLinks(<UserUrl 
                  itemsx={items}
                  links={photos.response} 
                  setModal={setModal} 
                  setSnackbar={setSnackbar}
                  setPopout={setPopout}
                  restart={restart}
                  token={token}
                  newOffset={newOffset}
                  offset={offset}
                  />)
            } catch (e) {
              console.log(e);
              go({currentTarget: {dataset: {to: 'new-user'}}})
            }
            
        }
          start()
    }, [upload, token])

    return (
    <Panel id={id}>
        <PanelHeader>Сокращение cсылок</PanelHeader>
        <Card mode="shadow" style={{margin: 5, padding: 5, fontSize: 14, textAlign: 'center'}}>
            <div >
На этой странице вы можете сделать из длинной и сложной ссылки простую.
Такие ссылки удобнее использовать в ваших записях и сообщениях.
            </div>
        </Card >
        <Input style={{margin: 5}} type="text" placeholder="Ссылка, которую вы хотите сократить" id='home-input'/>
        <Checkbox id='home-checkbox' onClick={() => {
          let it = 1
          if (blocked === 1) {
            it = 0
          }
          setBlocked(it)
        }}><p className='checkbox-p'>Статистика ссылки видна только вам</p></Checkbox>
        <Button size="m" style={{margin: 5}} onClick={newLink}>Сократить</Button>
        
        <Group>
        <Header mode="secondary"><p className='checkbox-p'>Недавно сокращённые ссылки</p></Header>
        {userLinks}
        </Group>
        {snackbar}
    </Panel>
    )
}

export default Home